import React, { useEffect, useRef, useState } from "react";
import Button, { OutlineButton } from "../button/Button";
import SwiperCore, {  Navigation, Pagination } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";
import tmdApi, { category, movieType } from "../../api/tmdbApi";
import apiConfig from "../../api/apiConfig";
import "./hero-slide.scss";
import { useNavigate } from "react-router-dom";
import Modal, { ModalContent } from "../modal/Modal";

// modules styles
import "swiper/components/navigation/navigation.min.css";
import "swiper/components/pagination/pagination.min.css";

const HeroSlide = () => {
  SwiperCore.use([Navigation, Pagination]);
  const [movieItems, setMovieItem] = useState([]);

  useEffect(() => {
    const getMovies = async () => {
      const params = { page: 1 };
      try {
        const response = await tmdApi.getMoviesList(movieType.popular, {
          params,
        });
        setMovieItem(response.results.slice(0, 5));
      } catch {
        console.log("Error");
      }
    };
    getMovies();
  }, []);

  return (
    <div className="hero-slide">
      <Swiper
        style={{
          "--swiper-navigation-color": "#f00",
          "--swiper-pagination-color": "#f00",
        }}
        grabCursor={true}
        spaceBetween={0}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        slidesPerView={1}
        loop={true}
        loopPreventsSlide={true}
      >
        {movieItems.map((item, i) => (
          <SwiperSlide key={i}>
            {({ isActive }) => (
              <HeroSlideItem
                item={item}
                className={`${isActive ? "active" : ""}`}
              />
            )}
          </SwiperSlide>
        ))}
      </Swiper>
      {movieItems.map((item, i) => (
        <TrailerModal key={i} item={item} />
      ))}
    </div>
  );
};

const HeroSlideItem = (props) => {
  let navigate = useNavigate();
  const item = props.item;
  const background = apiConfig.originalImage(
    item.backdrop_path ? item.backdrop_path : item.poster_path
  );
  const setModalActive = async () => {
    const modal = document.querySelector(`#modal_${item.id}`);
    const videos = await tmdApi.getVideos(category.movie, item.id);
    if (videos.results.length > 0) {
      const videoSrc = `https://www.youtube.com/embed/${videos.results[0].key}`;
      modal
        .querySelector(".modal-content > iframe")
        .setAttribute("src", videoSrc);
    } else {
      modal.querySelector(".modal-content").innerHTML = "No Trailer";
    }
    modal.classList.toggle("active");
  };
  return (
    <div
      className={`hero-slide-item ${props.className}`}
      style={{ backgroundImage: `url(${background})` }}
    >
      <div className="hero-slide-item-content container">
        <div className="info">
          <h2 className="title">{item.title}</h2>
          <p className="overview">{item.overview}</p>
          <div className="btns">
            <Button
              onClick={() => {
                navigate(`/movie/${item.id}`);
              }}
            >
              Watch Now
            </Button>
            <OutlineButton onClick={setModalActive}>
              Watch Trailer
            </OutlineButton>
          </div>
        </div>
        <div className="poster">
          <img src={apiConfig.w500Image(item.poster_path)} alt="" />
        </div>
      </div>
    </div>
  );
};

const TrailerModal = (props) => {
  const item = props.item;
  const iframeRef = useRef();
  const onClose = () => {
    iframeRef.current.setAttribute("src", "");
  };
  return (
    <Modal active={false} id={`modal_${item.id}`}>
      <ModalContent onClose={onClose}>
        <iframe
          ref={iframeRef}
          width="100%"
          height="500px"
          title="trailer"
        ></iframe>
      </ModalContent>
    </Modal>
  );
};

export default HeroSlide;
