import React from "react";
import "./footer.scss";
import bg from "../../assets/footer-bg.jpg";
import logo from "../../assets/tmovie.png";
import { Link } from "react-router-dom";

const Footer = () => {
  return (
    <div className="footer" style={{ backgroundImage: `url(${bg})` }}>
      <div className="footer-content container">
        <div className="logo">
          <img src={logo} alt="" />
          <Link to="peties">Peties</Link>
        </div>
        <div className="menus">
          <div className="menu">
            <Link to={"peties"}>Home</Link>
            <Link to={"peties"}>Contact us</Link>
            <Link to={"peties"}>Term of services</Link>
            <Link to={"peties"}> About us</Link>
          </div>
          <div className="menu">
            <Link to={"peties"}>Live </Link>
            <Link to={"peties"}>FAQ</Link>
            <Link to={"peties"}>Premium</Link>
            <Link to={"peties"}>Pravacy Policy</Link>
          </div>
          <div className="menu">
            <Link to={"peties"}>You must watch</Link>
            <Link to={"peties"}>Recent release</Link>
            <Link to={"peties"}>Top IDMB</Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Footer;
